

body {
    font-family: 'Roboto', sans-serif;
}

.card {
    width:15rem;
    margin:0.5rem
}

.settings {
    background-color: rgb(233, 230, 230);
    padding: 0.5rem;
}

.center {
    margin: auto;
    width:75%;
    padding:10px;
}

.gauge-holder {
    width:90%;
    margin:auto;
    margin-top: 3%;
}

.gauge {
    width:100%;
    max-width:250px;
    color: #009578;
    font-size: 150%;
}

.gauge-body {
    width:100%;
    height:0;
    padding-bottom: 50%;
    background: #b4c0be;
    position: relative;
    border-top-left-radius: 100% 200%;
    border-top-right-radius: 100% 200%;
    overflow: hidden;
}

.gauge-fill {
    position: absolute;
    top:100%;
    left:0;
    width: inherit;
    height:100%;
    background:#009578;
    transform-origin:center top;
    transform: rotate(0.25turn);
    transition: transform 0.2s ease-out;
}

.gauge-cover {
    width:75%;
    height:150%;
    background:white;
    border-radius: 50%;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translateX(-50%);
    
    /* Text */
    display:flex;
    align-items:center;
    justify-content: center;
    padding-bottom: 25%;
    box-sizing: border-box;
}


#navBarLogo {
   
    padding-left: 10px;
    padding-right:10px;
}

.menuBar {
    text-align: center;
}